.services{padding:70px 0}.night-theme .services{background:#11132d}.services__items{display:grid;grid-template-columns:1fr 1fr;grid-gap:35px}@media (max-width:1023px){.services__items{grid-template-columns:1fr;grid-gap:20px}}.services__item{position:relative}.services__item:after{content:"";position:absolute;display:block;top:50%;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%);left:0;background:var(--beige-color);width:2px;height:calc(100% - 80px)}.services__item-title{text-transform:uppercase;font-size:32px;color:var(--blue-color);padding-left:47px;font-weight:300;margin-bottom:45px}.night-theme .services__item-title{color:var(--white-color)}.services__item-list{padding-left:47px;margin-bottom:70px}.services__item-list-item{text-transform:uppercase;color:var(--blue-color);margin-bottom:26px;line-height:8px}.night-theme .services__item-list-item{color:var(--white-color)}@media (max-width:1279px){.services__item-list-item{line-height:14px}}.services__item-link{font-size:11px;text-transform:uppercase;font-weight:500;color:var(--beige-color);padding-left:43px;position:relative;cursor:pointer}.services__item-link:after{content:"";position:absolute;display:block;width:10px;height:10px;left:27px;top:3px;background:var(--beige-color);-webkit-transform:rotate(-90deg);-ms-transform:rotate(-90deg);transform:rotate(-90deg);-webkit-mask:url(src/images/icons/down-arrow.svg);mask:url(src/images/icons/down-arrow.svg);mask-repeat:no-repeat!important;-webkit-mask-repeat:no-repeat!important;mask-position:center center!important;-webkit-mask-position:center center!important;mask-size:contain!important;-webkit-mask-size:contain!important}