.cases{padding-top:70px}.night-theme .cases{background:#11132d}.cases__title{margin-bottom:30px;color:var(--blue-color)}.night-theme .cases__title{color:var(--white-color)}.cases__slide{height:650px;width:100%;background-size:cover!important;background-repeat:no-repeat!important;background-position:50%!important}.cases__slide-item{height:100%}.cases__slide-item,.cases__slide-item-footer{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start;-webkit-box-align:start;-ms-flex-align:start;align-items:flex-start}.cases__slide-item-footer{width:100%;margin-top:auto;padding-bottom:30px;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between}@media (max-width:1023px){.cases__slide-item-footer{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start;-webkit-box-align:start;-ms-flex-align:start;align-items:flex-start}}.cases__slide-item-footer-work{width:calc(50% - 100px);background:var(--beige-color);display:grid;grid-template-columns:2fr 1fr;-webkit-box-align:center;-ms-flex-align:center;align-items:center;grid-gap:10px;padding:10px 20px;height:60px}@media (max-width:1023px){.cases__slide-item-footer-work{width:100%;margin:10px 0;min-height:60px;height:auto}}.cases__slide-item-footer-work-client{color:var(--blue-color);text-transform:uppercase;font-size:15px;font-weight:500}.cases__slide-item-footer-work-type{color:var(--white-color);font-size:14px;text-align:right}.cases__slide-item-footer-services{width:calc(50% - 100px);background:var(--beige-color);color:var(--blue-color);padding:10px 20px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start;-webkit-box-align:start;-ms-flex-align:start;align-items:flex-start;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;height:60px}@media (max-width:1023px){.cases__slide-item-footer-services{width:100%;margin:10px 0;min-height:60px;height:auto}}.cases__slide-item-footer-services-item{color:var(--blue-color);text-transform:uppercase;font-size:13px;font-weight:500}