.feedback{padding:50px 0}.night-theme .feedback{background:#11132d}.feedback__title{text-align:center;font-size:42px;font-weight:700;margin-bottom:30px}.night-theme .feedback__title{color:var(--white-color)}.feedback__switch{display:grid;grid-gap:20px;grid-template-columns:1fr 1fr 1fr;margin-bottom:30px}@media (max-width:1023px){.feedback__switch{display:none}}.feedback__switch-item{height:70px;width:100%;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;font-size:16px;cursor:pointer;color:var(--blue-color);border-bottom:1px solid var(--beige-color);text-transform:uppercase;font-weight:700}.night-theme .feedback__switch-item{color:var(--white-color)}.feedback__switch-item--active{color:var(--beige-color);border-bottom:1px solid rgba(187,139,80,.3)}.night-theme .feedback__switch-item--active{color:var(--beige-color)}.feedback__form-fields{display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:20px}@media (max-width:1023px){.feedback__form-fields{grid-template-columns:1fr}}.feedback__form-fields--callback{grid-template-areas:"name phone time" "desc desc desc" "button . sign"}@media (max-width:1023px){.feedback__form-fields--callback{grid-template-areas:"name" "phone" "time" "desc" "button" "sign"}}.feedback__form-field--name{grid-area:name}.feedback__form-field--phone{grid-area:phone}.feedback__form-field--time{grid-area:time}.feedback__form-field--desc{grid-area:desc}.feedback__form-field--sign{grid-area:sign;text-align:right}.feedback__form-field--button{grid-area:button;background:var(--beige-color);height:65px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;color:var(--white-color);text-transform:uppercase;font-size:16px}.feedback__form-field-input{border:1px solid #eaf2f4;height:65px;padding-left:15px;font-size:16px;width:100%;background:var(--white-color)}.feedback__form-field-input--big{height:200px;padding-top:15px;font-size:16px;font-family:squad,sans-serif}.feedback__form-field-input:active,.feedback__form-field-input:focus{border:1px solid #fff;-webkit-box-shadow:0 0 5px 0 #eaf2f4;box-shadow:0 0 5px 0 #eaf2f4;outline:none}