.social{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.social__item{width:22px;height:22px;background:var(--beige-color);border-radius:50%;margin-right:7px;position:relative;cursor:pointer}.social__item:hover{background:var(--blue-color)}.social__item--tw:after{mask:url(src/blocks/social/assets/img/twitter.svg);-webkit-mask:url(src/blocks/social/assets/img/twitter.svg)}.social__item--fb:after{mask:url(src/blocks/social/assets/img/facebook.svg);-webkit-mask:url(src/blocks/social/assets/img/facebook.svg)}.social__item--vk:after{mask:url(src/blocks/social/assets/img/vk.svg);-webkit-mask:url(src/blocks/social/assets/img/vk.svg)}.social__item--ok:after{mask:url(src/blocks/social/assets/img/odnoklassniki.svg);-webkit-mask:url(src/blocks/social/assets/img/odnoklassniki.svg)}.social__item:after{content:"";position:absolute;display:block;left:50%;top:50%;-webkit-transform:translate(-50%,-50%);-ms-transform:translate(-50%,-50%);transform:translate(-50%,-50%);width:65%;height:65%;background:var(--white-color);mask-repeat:no-repeat!important;-webkit-mask-repeat:no-repeat!important;mask-position:center center!important;-webkit-mask-position:center center!important;mask-size:contain!important;-webkit-mask-size:contain!important}.social__item:last-of-type{margin-right:0}